.member-txt {
    font-size: 20px;
    top: 10px;
    font-weight: 100;
}

.members-container {
    position: relative;
    display: flex;
    flex-direction: column;
    justify-content: start;
    align-items: flex-start;
    min-height: 70vh;
    margin-top: 100px;
}

.members-container .member {
    position: relative;
    display: flex;
    justify-content: start;
    align-items: flex-start;
    width: 100%;
    margin-top: 15px;
    margin-bottom: 50px;
    background-color: rgba(0, 0, 0, 0.014);
    padding: 20px;
    border-radius: 50px;
}

.members-container .member .member-img {
    height: 300px;
    width: 300px;
    border-radius: 40px;
    background: url('../props/img/sayed.jpg');
    background-size: cover;
    background-position: center;
    border: 2px solid var(--color-sec-Light);
}

.members-container .member .member-info {
    margin-left: 50px;
    font-family: 'Open Sans Condensed';
    max-width: 50%;
}

.members-container .member .member-info .name {
    font-size: 40px;
    font-weight: 500;
    letter-spacing: 2px;
}

.members-container .member .member-info .position {
    font-size: 14px;
    font-weight: 400;
    Letter-spacing: 2px;
}

.members-container .member .member-info .about {
    margin-top: 40px;
    font-weight: 100;
    font-size: 17px;
}

.members-container .member .member-info .contact-member {
    background: var(--color-sec-Light);
    padding: 17px 35px;
    font-size: 18px;
    margin-top: 50px;
    border-radius: 50px;
    position: absolute;
    transition: .1s ease;
}


.members-container .member .member-info .contact-member:hover {
    background: transparent;
    border: 2px solid var(--color-sec-Light);
}

@media (max-width:640px) {
    .members-container .member {
        flex-direction: column;
        gap: 10px;
    }

    .members-container .member .member-img {
        height: 235px;
        width: 235px;
    }

    .member-txt {
        margin-left: 26px;
        font-size: 28px;
    }

    .members-container .member .member-info .contact-member {
        margin-left: -8px;
        padding: 17px 0;
    }
}