.contact-container {
    position: relative;
    display: flex;
    flex-direction: column;
    justify-content: start;
    align-items: flex-start;
    min-height: 80vh;
    margin-top: 100px;
    padding: 30px;
}

.contact-container h1 {
    margin: 0 auto;
    font-size: 22px;
    font-family: 'Open Sans Condensed';
    font-weight: 400;
    color: var(-- color-pri);
}

.contact-container .contact-links {
    position: relative;
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
    margin-top: 40px;
}

.contact-container .contact {
    position: inherit;
    height: 280px;
    width: 30%;
    background: Orgba(0, 0, 0, 0.322);
    border-radius: 20px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    transition: background .3s ease, transform .2s ease;
}

.contact-container .contact:hover {
    background: Orgba(255, 255, 255, 0.096);
    transform: scale(1.1);
}

.contact-container .contact .icon {
    position: relative;
    font-size: 130px;
}

.contact-container .contact h2 {
    position: relative;
    margin-top: 15px;
    font-size: 21px;
    font-weight: 500;
    display: flex;
    flex-direction: column;
    text-transform: capitalize;
    font-family: "Open Sans";
}

.contact-container .contact h2 span {
    font-size: 13px;
    font-weight: 100;
    margin-top: 3px;
    color: rgb(202, 202, 202)
}


@media (max-width:640px) {
    .contact-container .contact h2 span {
        font-size: 15px;
        margin-top: 9px;
    }

    .contact-container .contact h2 {
        font-size: 29px;
    }

    .contact-container .contact .icon {
        font-size: 166px;
    }

    .contact-container .contact-links {
        flex-direction: column;
    }
}